'use strict';

/**
 * Inner helper class that deals with the logic of the individual steps needed to setup an ad in the player.
 *
 * @param player {object} instance of the player that will play the ad. It assumes that the videojs-contrib-ads plugin
 *                        has been initialized when you use its utility functions.
 *
 * @constructor
 */

var VASTResponse = require('./VASTResponse');
var VASTError = require('./VASTError');
var VASTTracker = require('./VASTTracker');
var vastUtil = require('./vastUtil');

var async = require('../../utils/async');
var dom = require('../../utils/dom');
var playerUtils = require('../../utils/playerUtils');
var utilities = require('../../utils/utilityFunctions');

var logger = require('../../utils/consoleLogger');

function VASTIntegrator(player) {



    if (!(this instanceof VASTIntegrator)) {
        return new VASTIntegrator(player);
    }

    this.player = player;
}

VASTIntegrator.prototype.playAd = function playAd(vastResponse, callback) {
    var that = this;
    callback = callback || utilities.noop;

    if (!(vastResponse instanceof VASTResponse)) {
        return callback(new VASTError('On VASTIntegrator, missing required VASTResponse'));
    }

    async.waterfall([
        function (next) {
            next(null, vastResponse);
        },
        this._selectAdSource.bind(this),
        this._createVASTTracker.bind(this),
        this._addClickThrough.bind(this),
        this._addSkipButton.bind(this),
        this._setupEvents.bind(this),
        this._playSelectedAd.bind(this)
    ], function (error, response) {
        if (error && response) {
            that._trackError(error, response);
        }
        callback(error, response);
    });

    this._adUnit = {
        _src: null,
        type: 'VAST',
        pauseAd: function () {
            that.player.pause(true);
        },
        resumeAd: function () {
            that.player.play(true);
        },
        isPaused: function () {
            return that.player.paused(true);
        },
        getSrc: function () {
            return this._src;
        }
    };

    return this._adUnit;
};

VASTIntegrator.prototype._selectAdSource = function selectAdSource(response, callback) {
    var source;

    var playerWidth = dom.getDimension(this.player.el()).width;
    response.mediaFiles.sort(function compareTo(a, b) {
        var deltaA = Math.abs(playerWidth - a.width);
        var deltaB = Math.abs(playerWidth - b.width);
        return deltaA - deltaB;
    });

    source = this.player.selectSource(response.mediaFiles).source;

    if (source) {
        logger.info("selected source: ", source);
        if (this._adUnit) {
            this._adUnit._src = source;
        }
        return callback(null, source, response);
    }

    // code 403 <== Couldn't find MediaFile that is supported by this video player
    callback(new VASTError("Could not find Ad mediafile supported by this player", 403), response);
};

VASTIntegrator.prototype._createVASTTracker = function createVASTTracker(adMediaFile, response, callback) {
    try {
        callback(null, adMediaFile, new VASTTracker(adMediaFile.src, response), response);
    } catch (e) {
        callback(e, response);
    }
};

VASTIntegrator.prototype._setupEvents = function setupEvents(adMediaFile, tracker, response, callback) {
    var previouslyMuted;
    var player = this.player;
    player.on('fullscreenchange', trackFullscreenChange);
    player.on('vast.adStart', trackImpressions);
    player.on('pause', trackPause);
    player.on('timeupdate', trackProgress);
    player.on('volumechange', trackVolumeChange);

    playerUtils.once(player, ['vast.adEnd', 'vast.adsCancel'], unbindEvents);
    playerUtils.once(player, ['vast.adEnd', 'vast.adsCancel', 'vast.adSkip'], function (evt) {
        if (evt.type === 'vast.adEnd') {
            tracker.trackComplete();
        }
    });

    return callback(null, adMediaFile, response);

    /*** Local Functions ***/
    function unbindEvents() {
        player.off('fullscreenchange', trackFullscreenChange);
        player.off('vast.adStart', trackImpressions);
        player.off('pause', trackPause);
        player.off('timeupdate', trackProgress);
        player.off('volumechange', trackVolumeChange);
    }

    function trackFullscreenChange() {
        if (player.isFullscreen()) {
            tracker.trackFullscreen();
        } else {
            tracker.trackExitFullscreen();
        }
    }

    function trackPause() {
        //NOTE: whenever a video ends the video Element triggers a 'pause' event before the 'ended' event.
        //      We should not track this pause event because it makes the VAST tracking confusing again we use a
        //      Threshold of 2 seconds to prevent false positives on IOS.
        if (Math.abs(player.duration() - player.currentTime()) < 2) {
            return;
        }

        tracker.trackPause();
        playerUtils.once(player, ['play', 'vast.adEnd', 'vast.adsCancel'], function (evt) {
            if (evt.type === 'play') {
                tracker.trackResume();
            }
        });
    }

    function trackProgress() {
        var currentTimeInMs = player.currentTime() * 1000;
        tracker.trackProgress(currentTimeInMs);
    }

    function trackImpressions() {
        tracker.trackImpressions();
        tracker.trackCreativeView();
    }

    function trackVolumeChange() {
        var muted = player.muted();
        if (muted) {
            tracker.trackMute();
        } else if (previouslyMuted) {
            tracker.trackUnmute();
        }
        previouslyMuted = muted;
    }
};

VASTIntegrator.prototype._addSkipButton = function addSkipButton(source, tracker, response, callback) {
    var skipOffsetInSec;
    var that = this;

    if (utilities.isNumber(response.skipoffset)) {
        skipOffsetInSec = response.skipoffset / 1000;
        addSkipButtonToPlayer(this.player, skipOffsetInSec);
    }
    callback(null, source, tracker, response);

    /*** Local function ***/
    function addSkipButtonToPlayer(player, skipOffset) {
        var skipButton = createSkipButton(player);
        var updateSkipButton = updateSkipButtonState.bind(that, skipButton, skipOffset, player);

        player.el().appendChild(skipButton);
        player.on('timeupdate', updateSkipButton);

        playerUtils.once(player, ['vast.adEnd', 'vast.adsCancel'], removeSkipButton);

        function removeSkipButton() {
            player.off('timeupdate', updateSkipButton);
            dom.remove(skipButton);
        }
    }

    function createSkipButton(player) {
        var skipButton = window.document.createElement("div");
        dom.addClass(skipButton, "vast-skip-button");

        skipButton.onclick = function (e) {
            if (dom.hasClass(skipButton, 'enabled')) {
                tracker.trackSkip();
                player.trigger('vast.adSkip');
            }

            //We prevent event propagation to avoid problems with the clickThrough and so on
            if (window.Event.prototype.stopPropagation !== undefined) {
                e.stopPropagation();
            } else {
                return false;
            }
        };

        return skipButton;
    }

    function updateSkipButtonState(skipButton, skipOffset, player) {
        var timeLeft = Math.ceil(skipOffset - player.currentTime());
        if (timeLeft > 0) {
            var text = player.options_.plugins['ads-setup'].skipInButtonText ? player.options_.plugins['ads-setup'].skipInButtonText : "Skip in "
            skipButton.innerHTML = text + utilities.toFixedDigits(timeLeft, 2) + "...";
        } else {
            if (!dom.hasClass(skipButton, 'enabled')) {

                dom.addClass(skipButton, 'enabled');
                skipButton.innerHTML = player.options_.plugins['ads-setup'].skipButtonText ? player.options_.plugins['ads-setup'].skipButtonText : "Skip ad";
            }
        }
    }
};

VASTIntegrator.prototype._addClickThrough = function addClickThrough(mediaFile, tracker, response, callback) {
    var player = this.player;
    var blocker = createClickThroughBlocker(player, tracker, response);
    var updateBlocker = updateBlockerURL.bind(this, blocker, response, player);

    player.el().insertBefore(blocker, player.controlBar.el());
    player.on('timeupdate', updateBlocker);
    playerUtils.once(player, ['vast.adEnd', 'vast.adsCancel'], removeBlocker);

    return callback(null, mediaFile, tracker, response);

    /*** Local Functions ***/

    function createClickThroughBlocker(player, tracker, response) {
        var blocker = window.document.createElement("a");
        var clickThroughMacro = response.clickThrough;

        dom.addClass(blocker, 'vast-blocker');
        blocker.href = generateClickThroughURL(clickThroughMacro, player);

        if (utilities.isString(clickThroughMacro)) {
            blocker.target = "_blank";
        }

        blocker.onclick = function (e) {
            if (player.paused()) {
                player.play();

                //We prevent event propagation to avoid problems with the player's normal pause mechanism
                if (window.Event.prototype.stopPropagation !== undefined) {
                    e.stopPropagation();
                }
                return false;
            }

            player.pause();
            tracker.trackClick();
        };

        return blocker;
    }

    function updateBlockerURL(blocker, response, player) {
        blocker.href = generateClickThroughURL(response.clickThrough, player);
    }

    function generateClickThroughURL(clickThroughMacro, player) {
        var variables = {
            ASSETURI: mediaFile.src,
            CONTENTPLAYHEAD: vastUtil.formatProgress(player.currentTime() * 1000)
        };

        return clickThroughMacro ? vastUtil.parseURLMacro(clickThroughMacro, variables) : '#';
    }

    function removeBlocker() {
        player.off('timeupdate', updateBlocker);
        dom.remove(blocker);
    }
};

VASTIntegrator.prototype._playSelectedAd = function playSelectedAd(source, response, callback) {
    var player = this.player;

    player.preload("auto"); //without preload=auto the durationchange event is never fired
    player.src(source);

    logger.debug("<VASTIntegrator._playSelectedAd> waiting for durationchange to play the ad...");

    playerUtils.once(player, ['durationchange', 'error', 'vast.adsCancel'], function (evt) {
        if (evt.type === 'durationchange') {
            logger.debug("<VASTIntegrator._playSelectedAd> got durationchange; calling playAd()");
            playAd();
        } else if (evt.type === 'error') {
            callback(new VASTError("on VASTIntegrator, Player is unable to play the Ad", 400), response);
        }
        //NOTE: If the ads get canceled we do nothing/
    });

    /**** local functions ******/
    function playAd() {

        playerUtils.once(player, ['playing', 'vast.adsCancel'], function (evt) {
            if (evt.type === 'vast.adsCancel') {
                return;
            }

            logger.debug("<VASTIntegrator._playSelectedAd/playAd> got playing event; triggering vast.adStart...");

            player.trigger('vast.adStart');

            player.on('ended', proceed);
            player.on('vast.adsCancel', proceed);
            player.on('vast.adSkip', proceed);

            function proceed(evt) {

                if (evt.type === 'ended' && (player.duration() - player.currentTime()) > 3) {
                    // Ignore ended event if the Ad time was not 'near' the end
                    // avoids issues where IOS controls could skip the Ad
                    return;
                }

                player.off('ended', proceed);
                player.off('vast.adsCancel', proceed);
                player.off('vast.adSkip', proceed);

                //NOTE: if the ads get cancel we do nothing apart removing the listners
                if (evt.type === 'ended' || evt.type === 'vast.adSkip') {
                    callback(null, response);
                }
            }
        });

        logger.debug("<VASTIntegrator._playSelectedAd/playAd> calling player.play()...");

        player.play();
    }
};

VASTIntegrator.prototype._trackError = function trackError(error, response) {
    vastUtil.track(response.errorURLMacros, {ERRORCODE: error.code || 900});
};

module.exports = VASTIntegrator;